.preloader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  background: -webkit-linear-gradient(left, rgba(0, 0, 0, 0.8) 20%, rgba(0, 0, 0, 0.4)) 100%, -webkit-linear-gradient(315deg, #002df0, #0014dd);
  background: -o-linear-gradient(left, rgba(0, 0, 0, 0.8) 20%, rgba(0, 0, 0, 0.4)) 100%, -o-linear-gradient(315deg, #002df0, #0014dd);
  background: linear-gradient(90deg, rgba(0, 0, 0, 0.8) 20%, rgba(0, 0, 0, 0.4)) 100%, linear-gradient(135deg, #002df0, #0014dd);
  visibility: hidden;
  opacity: 0;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
  z-index: 99999999999999; }
  .preloader .logo {
    height: 40px; }
  .preloader.show {
    visibility: visible !important;
    opacity: 1 !important;
    -webkit-transition: all 0.5s ease;
    -o-transition: all 0.5s ease;
    transition: all 0.5s ease; }

.preloader_inner {
  text-align: center;
  font-size: 3em;
  color: #fff; }



